@media only print {
  body {
    color: #333;
    font-size: 16px;
    background: #fff;
    background-image: none; }
  .site-branding {
    float: left; }
  .site-header,
  .post-navigation,
  aside,
  .site-footer {
    display: none !important; }
  .page-masthead {
    padding: 0 !important; }
  .site-footer {
    background-image: none;
    color: black; }
  .off-canvas__inner,
  .page-wrap {
    padding-top: 0; }
  main {
    width: 100% !important; }
  h1, h2 {
    page-break-before: always; }
  img {
    max-width: 500px;
    width: 100% !important; }
  a {
    text-decoration: underline; }
  ul, img {
